.sign-in-form-container {
  height: calc(100vh - 40px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  border-radius: 10px;
  background-color: rgba(84, 84, 92, 0.116); /* More transparent dark background */
  color: #fff; /* White text */
  text-shadow: 0 0 2px rgba(0, 0, 0, 0.3); /* Slight text shadow */
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2); /* Subtle box shadow */
}

.futuristic-form {
  margin-bottom: 20px;
  padding: 20px;
  border: none;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.418); /* More transparent background */
  box-shadow: inset 0 0 10px rgba(255, 255, 255, 0.05),
             0 0 20px rgba(0, 0, 255, 0.1); /* Inner and outer glow effects (adjusted transparency) */
}

.futuristic-form label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
}

.futuristic-form input {
  padding: 10px;
  border: none;
  border-radius: 5px;
  margin-bottom: 15px;
  width: 90%;
  background-color: rgba(0, 0, 0, 0.05); /* Very transparent input background */
  color: #fff; /* White text */
  text-shadow: 0 0 2px rgba(0, 0, 0, 0.3); /* Slight text shadow */
}

.futuristic-form button {
  background-color: rgba(0, 123, 255, 0.3); /* Blue background with transparency */
  color: #fff; /* White text */
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2); /* Subtle button shadow */
  transition: all 0.2s ease-in-out; /* Smooth transition on hover */
}

.futuristic-form button:hover {
  background-color: rgba(0, 105, 229, 0.4); /* Darker blue on hover with transparency */
}

.futuristic-button {
  background-color: transparent; /* Transparent background */
  color: #fff; /* White text */
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none; /* Remove underline from button text */
  transition: all 0.2s ease-in-out; /* Smooth transition on hover */
}

.futuristic-button:hover {
  color: #007bff; /* Blue text on hover */
  text-shadow: 0 0 5px rgba(0, 0, 255, 0.2); /* Add blue text shadow on hover */
}
