.logo{
    padding: 8px;
    margin: 16px;
    border-radius: 24px;
    background-color: rgba(255, 0, 0, 0.164);
    height: 150px;
    width:  150px;
    transition-duration: 50ms;

}
.logo:hover{
transform: scale(1.05,1.05) rotateZ(1deg);
}

