input {
    background-color: rgba(254, 185, 185, 0.395);
    /* background: linear-gradient(to left, rgba(255, 74, 74, 0.395), rgba(113, 192, 252, 0.312)); */
border: solid black;
border-radius: 16px;
margin: 8px;
}

button{
background: rgba(25, 186, 240, 0.505);
padding: 2px;
font-weight: 600;
border-width: 2px;
border-style: solid;
border-radius: 8px;
    margin: 8px;
transition: all 200ms ;

}

button:hover{
transform: scale(1.07);
cursor: pointer;
}

.imgbox{
background-color: rgba(0, 0, 0, 0.314);
padding: 8px;
border-radius: 8px;
box-shadow: 4px 4px 8px rgb(0, 0, 0);
}